@import "amazon";

// ヘルプボタン
.help-tip{
  padding: 1px 5px;
  @include border-radius(10px);
  margin-left: 1em;
}

// 半透明のボタン
.btn-trans{
  background-color: rgba(0, 0, 0, .4);
  @include border-radius(0);
  border: 2px solid $inverse-body;
  color: $inverse-body;
  &:hover{
    color: $inverse-body;
    background-color: #000;
  }
}

.btn-amazon{
  i{
    color: orange;
  }
}

// 回転
.rotation{
  @include animation(rotation 1.5s linear infinite);
}

// サムネイル
.pseudo-thumbnail{
  background-size: cover;
  background-color: #737373;
  background-position: center center;
}

.pager{
  a{
    max-width: 45%;
  }
}
@media only screen and (max-width: $screen-xs-max) {
  .pager{
    font-size: 12px;
  }
}
.has-feedback{
  .icon-checkmark,
  .icon-spam,
  .icon-close,
  .icon-loop3{
    display: none;
  }
  .help-text{
    display: none;
    color: $inverse-body;
  }
  &.loading{
    .icon-loop3{
      display: block;
      color: $more-shade-font;
    }
  }
  &.has-warning{
    .icon-spam{
      display: block;
    }
    .help-warning{
      display: inline;
      background-color: $brand-warning;
    }
  }
  &.has-error{
    .icon-close{
      display: block;
    }
    .help-error{
      display: inline;
      background-color: $brand-danger;
    }
  }
  &.has-success{
    .icon-checkmark{
      display: block;
    }
    .help-success{
      display: inline;
      background-color: $brand-success;
    }
  }
}

// ツールチップ
.tooltip-inner{
  text-align: left;
}

// タグコンテナ
.tag-container{
  a{
    margin-top: 5px;
    margin-right: 5px;
    @extend .btn;
    @extend .btn-default;
    @extend .btn-xs;
    i{
      &:before{
        @include hameticon();
        @extend .icon-plus-circle2:before;
      }
      vertical-align: middle;
      color: $clear-font;
      &:hover{
        color: $brand-success;
      }
    }
    &.me{
      @extend .btn-danger;
      i{
        &:before{
          @extend .icon-close3:before;
        }
        color: inherit;
        &:hover{
          color: darken($brand-danger, 20);
        }
      }
    }
  }
  & + .alert{
    display: none;
  }
  &.no-tag + .alert{
    display: block;
  }
}
// スピナー
.ui-autocomplete-input{
 & + .form-control-feedback{
   top: 0;
   right: 50px;
   color: $more-shade-font;
   display: none;
 }
}
.ui-autocomplete-loading{
  & + .form-control-feedback{
    @include inline-block();
  }
}

.container-subtitle{
  font-size: 14px;
  font-weight: bold;
  border-bottom: 1px dotted $more-shade-font;
  margin: 1em 0.5em;
  color: $shade-font;
  padding-bottom: 0.5em;
}

//
// ジャンボトロン
//
// --------------------
//
.jumbotron{
  position: relative;
  overflow: hidden;
  background-position: center center;
  background-size: cover;
  &:after{
    z-index: 1;
    position: absolute;
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.2);
  }
  h1, p{
    position: relative;
    z-index: 2;
    color: $inverse-body;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.6);
  }
  .btn{
    @extend .btn-trans;
  }
}
@media only screen and (max-width: $screen-sm-max) {
  .jumbotron{
    h1{
      font-size: 48px;
    }
    p{
      font-size: 14px;
    }
  }
}
@media only screen and (max-width: $screen-xs-max) {
  .jumbotron{
    h1{
      font-size: 32px;
    }
  }
}


//
// 著者情報
//
// --------------------
//

// 個別管理するためのミックスイン
@mixin author-profile($image-size){
  $image-pad: $image-size / 4;
  padding: $image-pad $image-pad $image-pad $image-pad * 2 + $image-size;
  margin: $image-pad 0;
  .avatar{
    width: $image-size;
    height: $image-size;
    left: $image-pad;
    margin-top: -0.5 * $image-size;
  }
}

// 共通
.author-profile{
  $border: 1px solid $navbar-default-border;
  background-color: $navbar-default-bg;
  border-top: $border;
  border-bottom: $border;
  position: relative;
  h3{
    margin-top: 0;
  }
  .avatar{
    position: absolute;
    @extend .img-circle;
    top: 50%;
  }
  @include author-profile(80px);
}

// スマートフォン
@media only screen and (max-width: $screen-xs-max) {
  .author-profile{
    h3 small{
      display: block;
    }
    @include author-profile(60px);
  }
}


//
// メタデータ用のインラインリスト
//
// --------------------
//
ul.metadata{
  border-bottom: 1px solid #ddd;
  color: #999;
  padding: 7px 15px;
  margin-bottom: 20px;
  li{
    border-left: 1px solid #ddd;
    padding: 0.25em 0.5em;
    &:first-child{
      border-left: none;
    }
  }
}
@media only screen and (max-width: $screen-xs-max) {
  ul.metadata li:first-child{
    border-left: 1px solid #ddd;
  }
}



//
// 一覧ページのヘッダー
//
// --------------------
//
.archive-meta{
  h1{
    font-size: 28px;
    font-family: $work-title;
    .label{
      font-family: sans-serif;
      margin-left: 0.5em;
    }
  }
  .desc{
    font-family: $work-title;
    p{
      margin-bottom:1em;
      color: $little-shade-font;
      padding-left: 1em;
      padding-right: 1em;
      &:first-child{
        padding-top: 2em;
        margin-top: 2em;
        border-top: $divide-border;
      }
      &:last-child{
        padding-bottom: 2em;
        margin-bottom: 2em;
        border-bottom: $divide-border;
      }
    }
  }
}
.meta{
  &--series{
    margin-bottom: 1em;
    border-bottom: $divide-border;
    padding-bottom: 1em;
  }
  &__thumbnail{
    padding-top: 0.5em;
    text-align: center;
    img{
      max-width: 100%;
      height: auto;
      width: auto;
    }
  }
}


//
// 一覧ページのリスト
//
// --------------------
//
// タブ
.nav-tabs--archive{
  li{
    .icon-arrow-up{
      display: none;
    }
    &.active{
      a:hover{
        cursor: pointer;
        .icon-arrow-up{
          display: inline-block;
        }
        .icon-arrow-down{
          display: none;
        }
      }
    }
    &.asc{
      .icon-arrow-up{
        display: inline-block;
      }
      .icon-arrow-down{
        display: none;
      }
      &.active{
        a:hover{
          .icon-arrow-down{
            display: inline-block;
          }
          .icon-arrow-up{
            display: none;
          }
        }
      }
    }
  }
}
// コンテナ
.archive-container{
  margin: 20px 0;
  border-top: 1px solid $thin-border;
  // タブが上にある場合はマージンなし
  .nav-tabs--archive + .tab-content &{
    border-top: none;
    margin-top: 0;
  }
  & > li{
    margin-top: 0;
    padding: 0;
    border-bottom: 1px solid $thin-border;
    &:nth-child(even){
      background-color: $zebra-bg;
    }
    .media__link{
      @extend .clearfix;
      display: block;
      padding: 1em;
      @media only screen and (max-width: $screen-sm-max) {
        padding: 1em 0.5em;
      }
        &:link{
        text-decoration: none;
      }
      &:visited{
        text-decoration: none;
      }
      &:hover{
        background: $link-active-bg;
        text-decoration: none;
      }
      &:active{
        color: $link-active;
        background: #fffecc;
        text-decoration: none;
      }
    }
  }
  // 見出し
  h3, h2{
    margin-top: 0;
  }
  // アバター
  .pull-left, .pull-right{
    img{
      height: auto;
      width: auto;
      max-width: 160px;
      @media only screen and (max-width: $screen-sm-max){
        max-width: 80px;
      }
    }
    .avatar{
      width: 80px;
      height: 80px;
      @extend .img-circle;
    }
  }
  .pull-left{
    margin-right: 1em;
  }
  .pull-right{
    margin-left: 1em;
  }
  .author-info .avatar{
    width: 20px;
    height: 20px;
    @extend .img-circle;
  }
  // リスト（メタ情報）
  ul.list-inline{
    color: #666;
    margin-bottom: 10px;
    i{
      color: #aaa;
    }
    li{
      border-left: 1px solid #ddd;
      &:first-child{
        border-left: none;
      }
    }
  }
}

// リード
.excerpt{
  margin: 2em 1em;
  p{
    @extend .lead;
  }
}

// シングルページのページャ
.post-pager{
  margin: 20px 0;
}

// ページネーション
.link-pages{
  margin: 1.5em 0 2em !important;
  text-align: center;
  font-family: sans-serif;
  text-indent: 0 !important;
  & > span, a{
    font-size: 12px;
    @include inline-block();
    margin-left: 0.5em;
    padding: 0.4em 0.75em;
    @include border-radius(2px);
    color: #fff;
  }
  & > span{
    background-color: $shade-font;
  }
  a{
    &:link, &:visited{
      background-color: $link-color;
      text-decoration: none;
    }
    &:hover{
      background-color: $link-active;
      text-decoration: none;
    }
    &:active{
      background-color: lighten($link-active, 10);
      text-decoration: none;
    }
  }
}

//
// シェアパネル
//
// ---------------
//

.row--share{

  & + .nav{
    margin-top: 20px;
  }

  & + div{
    margin-top: 20px;
  }

  // ボタンの後
  .btn--joinus + &{
    margin: 10px 0;
  }

  .col-xs-4, .col-sm-2{
    padding: 0;
  }

  .secondary{
    height: 40px;
    line-height: 40px;
    color: $hametuha-color;
  }
  a{
    display: block;
    height: 40px;
    line-height: 40px;
    &:link, &:visited{
      color: #fff;
      text-decoration: none;
    }
    &:active{
      color: #fff;
      opacity: .8;
    }
    @each $var in (
      facebook $facebook-color,
      twitter $twitter-color,
      googleplus $google-color,
      hatena $hatena-color,
      line $line-color,
      pocket $pocket-color,
    ){
      $brand: nth($var, 1);
      $color: nth($var, 2);
      &.share--#{$brand}{
        background-color: $color;
      }
    }
    span{
      margin-left: 0.5em;
    }
  }
  i{
    color: #fff !important;
    line-height: 1;
    font-size: 24px;
    vertical-align: middle;
  }
}

//
// イベント情報
//
// ---------------
//
.event-detail{
  .dl-horizontal{
    border-top: 2px solid $thin-border;
    border-bottom: 2px solid $thin-border;
    padding: 15px;
    dt, dd{
      margin-bottom: 10px;
    }
  }
}

@media only screen and (max-width: $screen-xs-max) {
  .event-detail{
    .dl-horizontal{
      dt{
        margin-bottom: 5px;
      }
    }
  }
}

caption.outdated{
  @extend .text-danger;
}

#gmap-announcement{
  height: 250px;
  background-color: $navbar-default-bg;
  margin-bottom: 20px;
}

// フィードバック
.afb_container{
  .good, .bad{
    @extend .btn;
    @extend .btn-sm;
    &:link{
      text-decoration: none;
    }
  }
  .good{
    @extend .btn-success;
  }
  .bad{
    @extend .btn-default;
  }
}

// フォームのローディング
form{
  position: relative;
  &.loading:before{
    content: '';
    position: absolute;
    z-index: 100;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: white;
    background-color: rgba(255, 255, 255, .8);
  }
  &.loading:after{
    @extend .icon-file-upload:before;
    font-family: "hameticon";
    font-size: 80px;
    line-height: 1;
    position: absolute;
    z-index: 101;
    top: 50%;
    left: 50%;
    margin: -40px 0 0 -40px;
    color: $more-shade-font;
  }
}

